.ccl-test-container {
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto;

  h2 {
    color: #333;
    margin-bottom: 10px;
  }

  .description {
    color: #666;
    margin-bottom: 20px;

    code {
      background-color: #f5f5f5;
      padding: 2px 6px;
      border-radius: 3px;
      font-family: monospace;
      color: #d73a49;
    }
  }

  .button-group {
    display: flex;
    gap: 10px;
    margin-bottom: 20px;
  }

  .btn {
    padding: 10px 20px;
    border: none;
    border-radius: 4px;
    font-size: 14px;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.2s ease;

    &:disabled {
      opacity: 0.6;
      cursor: not-allowed;
    }

    &.btn-primary {
      background-color: #0078d4;
      color: white;

      &:hover:not(:disabled) {
        background-color: #005a9e;
      }
    }

    &.btn-secondary {
      background-color: #6c757d;
      color: white;

      &:hover {
        background-color: #5a6268;
      }
    }
  }

  .status-message {
    padding: 15px;
    border-radius: 4px;
    margin-bottom: 20px;

    &.loading {
      background-color: #e7f3ff;
      border: 1px solid #b3d9ff;
      color: #0056b3;
      display: flex;
      align-items: center;
      gap: 10px;

      .spinner {
        width: 20px;
        height: 20px;
        border: 3px solid #b3d9ff;
        border-top-color: #0056b3;
        border-radius: 50%;
        animation: spin 1s linear infinite;
      }
    }

    &.error {
      background-color: #f8d7da;
      border: 1px solid #f5c6cb;
      color: #721c24;

      h3 {
        margin-top: 0;
        margin-bottom: 10px;
        color: #721c24;
      }

      pre {
        margin: 0;
        white-space: pre-wrap;
        word-wrap: break-word;
      }
    }
  }

  .results-container {
    background-color: #f8f9fa;
    border: 1px solid #dee2e6;
    border-radius: 4px;
    padding: 15px;

    h3 {
      margin-top: 0;
      margin-bottom: 15px;
      color: #333;
    }

    .results-content {
      background-color: white;
      border: 1px solid #dee2e6;
      border-radius: 4px;
      padding: 15px;
      max-height: 500px;
      overflow: auto;

      pre {
        margin: 0;
        font-family: 'Courier New', monospace;
        font-size: 12px;
        line-height: 1.5;
        white-space: pre-wrap;
        word-wrap: break-word;
        color: #333;
      }
    }
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
